.asideCard {
    display: flex;
    flex-direction: column;
    width: 100%;

    &__banner {
        height: 150px;
    }

    &__content {
        display: flex;
        flex-direction: row;
        justify-content: flex-start;
        padding: 10px 20px;
        background-color: #fff;
        border: 1px solid #e9e9e9;

        div {
            flex: 1;

            h3 {
                font-family: Arial, Helvetica, sans-serif;
                font-size: 14px;
                font-weight: 600;
                line-height: 1;
                margin: 0 0 8px;
                color: #161616;
            }

            p {
                font-family: Arial, Helvetica, sans-serif;
                color: #909090;
                font-size: 12px;
                font-weight: 600;
                line-height: 1;
                margin: 0;
            }
        }

        img {
            border-radius: 40px;
        }
    }
}